import React from "react"
import { graphql, Link, useStaticQuery } from "gatsby"
import { Helmet } from "react-helmet"

import Layout from "../components/Layout"
import "./blogTemplate.css"

export default function Template({ data }) {
  const post = data.markdownRemark
  const { title, author, date } = post.frontmatter
  const { site } = useStaticQuery(BlogPostAndSEO)

  const { defaultImage, siteUrl } = site.siteMetadata

  const seo = {
    image: `${siteUrl}${defaultImage}`,
  }

  return (
    <Layout>
      <Helmet>
        <meta
          property="og:title"
          content="Scott Sandman | Full Stack Developer"
        />
        <meta property="og:image" content={seo.image} />
        <meta
          property="og:image:alt"
          content="Should be a picture of Charleston, SC here"
        />
        <meta
          property="og:description"
          content="A blog detailing my learning experiences on the path to becoming a Full Stack Developer"
        />
        <meta property="og:url" content="https://scottsandman.github.io/" />
        <meta
          property="twitter:title"
          content="Scott Sandman | Full Stack Developer"
        />
        <meta property="twitter:image" content={seo.image} />
        <meta
          property="twitter:image:alt"
          content="Should be a picture of Charleston, SC here"
        />
        <meta
          property="twitter:description"
          content="A blog detailing my learning experiences on the path to becoming a Full Stack Developer"
        />
        <meta property="twitter:card" content="summary_large_image" />
      </Helmet>
      <div className="blogTemplate">
        <Link to="/">Back to Blogs</Link>
        <h1 className="blogTemplate-title">{title}</h1>
        <p className="blogTemplate-posted-by">
          Posted by {author} on {date}
        </p>
        <div dangerouslySetInnerHTML={{ __html: post.html }} />
      </div>
    </Layout>
  )
}

export const postQuery = graphql`
  query BlogPostAndSEO($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      frontmatter {
        author
        date
        title
        path
      }
      html
    }
    site {
      siteMetadata {
        siteImage: image
        siteUrl: siteUrl
      }
    }
  }
`
